"use client";

import * as React from "react";
import Link from "next/link";
import { useQuery } from "@tanstack/react-query";
import type { ColumnDef } from "@tanstack/react-table";

import { api } from "@/lib/api";
import { DataGrid } from "@/components/tables/data-grid";
import {
  colMono,
  colPercent,
  colNumber,
  hashShort,
} from "@/components/tables/column-helpers";
import { ExecutionScoreBadge } from "@/components/ui/ExecutionScoreBadge";

/** Row shape we render (normalized from API) */
type CatalogRow = {
  creator_pubkey: string;
  alias?: string | null;
  copyability_tier?: string | null;
  risk_score?: number | null;
  creator_rank?: number | null;

  trades_copied_24h?: number | null;
  trades_copied_7d?: number | null;
  trades_copied_30d?: number | null;

  avg_execution_score_7d?: number | null;
  avg_execution_score_30d?: number | null;

  delta_slots_7d?: number | null;
  delta_slots_30d?: number | null;

  roi_me_7d?: number | null;
  roi_me_30d?: number | null;
};

/** Normalize backend list to CatalogRow[] */
function normalizeCatalog(rows: any[]): CatalogRow[] {
  if (!Array.isArray(rows)) return [];
  return rows.map((r: any) => ({
    creator_pubkey: String(r.creator_pubkey ?? r.pubkey ?? ""),
    alias: r.alias ?? r.name ?? null,
    copyability_tier: r.copyability_tier ?? r.tier ?? null,
    risk_score: r.risk_score ?? null,
    creator_rank: r.creator_rank ?? null,

    trades_copied_24h: r.trades_copied_24h ?? null,
    trades_copied_7d: r.trades_copied_7d ?? null,
    trades_copied_30d: r.trades_copied_30d ?? null,

    avg_execution_score_7d: r.avg_execution_score_7d ?? null,
    avg_execution_score_30d: r.avg_execution_score_30d ?? null,

    delta_slots_7d: r.delta_slots_7d ?? null,
    delta_slots_30d: r.delta_slots_30d ?? null,

    roi_me_7d: r.roi_me_7d ?? null,
    roi_me_30d: r.roi_me_30d ?? null,
  }));
}

/** Columns for the grid */
function useColumns(): ColumnDef<CatalogRow, any>[] {
  return React.useMemo<ColumnDef<CatalogRow, any>[]>(() => {
    return [
      {
        id: "creator",
        header: "Creator",
        accessorKey: "creator_pubkey",
        cell: (ctx) => {
          const v = ctx.getValue() as string;
          const alias = (ctx.row.original.alias ?? "") as string;
          return (
            <div className="min-w-[200px]">
              <Link
                href={`/creators/${encodeURIComponent(v)}`}
                className="font-medium hover:underline"
              >
                {alias || hashShort(v)}
              </Link>
              <div className="text-xs text-muted-foreground font-mono">
                {hashShort(v)}
              </div>
            </div>
          );
        },
      },

      colMono<CatalogRow>("copyability_tier", "Tier", {
        enableSorting: true,
      }),

      colNumber<CatalogRow>("risk_score", "Risk", 0, {
        enableSorting: true,
      }),

      colNumber<CatalogRow>("creator_rank", "Rank", 0, {
        enableSorting: true,
      }),

      colNumber<CatalogRow>("trades_copied_24h", "24h", 0),
      colNumber<CatalogRow>("trades_copied_7d", "7d", 0),
      colNumber<CatalogRow>("trades_copied_30d", "30d", 0),

      {
        id: "exec_7d",
        header: "Exec 7d",
        accessorKey: "avg_execution_score_7d",
        cell: (ctx) => (
          <ExecutionScoreBadge
            score={
              typeof ctx.getValue() === "number"
                ? (ctx.getValue() as number)
                : null
            }
          />
        ),
        enableSorting: true,
      },

      {
        id: "exec_30d",
        header: "Exec 30d",
        accessorKey: "avg_execution_score_30d",
        cell: (ctx) => (
          <ExecutionScoreBadge
            score={
              typeof ctx.getValue() === "number"
                ? (ctx.getValue() as number)
                : null
            }
          />
        ),
        enableSorting: true,
      },

      colNumber<CatalogRow>("delta_slots_7d", "ΔSlots 7d", 0, {
        enableSorting: true,
      }),
      colNumber<CatalogRow>("delta_slots_30d", "ΔSlots 30d", 0, {
        enableSorting: true,
      }),

      colPercent<CatalogRow>("roi_me_7d", "ROI 7d", 1, {
        enableSorting: true,
      }),
      colPercent<CatalogRow>("roi_me_30d", "ROI 30d", 1, {
        enableSorting: true,
      }),
    ];
  }, []);
}

export default function CreatorDirectoryPage() {
  // Simple local filter widgets
  const [q, setQ] = React.useState("");
  const [tier, setTier] = React.useState<string>("ALL");
  const [sort, setSort] = React.useState<string>("rank");

  // Build query params for backend (kept minimal; adjust as your API expects)
  const params = React.useMemo(() => {
    const p = new URLSearchParams();
    if (q) p.set("q", q);
    if (tier !== "ALL") p.set("tier", tier);
    p.set("sort", sort);
    p.set("limit", "300");
    return p;
  }, [q, tier, sort]);

  const { data = [], isLoading, error, refetch } = useQuery({
    queryKey: ["creators", "catalog", q, tier, sort],
    queryFn: () => api.getCreatorsCatalog(params),
    placeholderData: () => [],
  });

  const rows = React.useMemo(() => normalizeCatalog(data), [data]);
  const columns = useColumns();

  return (
    <div className="p-4 max-w-[1400px] mx-auto">
      <h1 className="text-xl font-semibold mb-3">Creator Directory</h1>

      {/* Filters */}
      <div className="mb-3 grid gap-2 md:grid-cols-3">
        <input
          className="w-full rounded-md border bg-background px-3 py-1.5 text-sm"
          placeholder="Search alias/pubkey…"
          value={q}
          onChange={(e) => setQ(e.target.value)}
        />
        <select
          className="w-full rounded-md border bg-background px-3 py-1.5 text-sm"
          value={tier}
          onChange={(e) => setTier(e.target.value)}
        >
          <option value="ALL">All tiers</option>
          <option value="T1">Tier 1</option>
          <option value="T2">Tier 2</option>
          <option value="T3">Tier 3</option>
        </select>
        <select
          className="w-full rounded-md border bg-background px-3 py-1.5 text-sm"
          value={sort}
          onChange={(e) => setSort(e.target.value)}
        >
          <option value="rank">Sort by Rank</option>
          <option value="exec">Sort by Exec 7d</option>
          <option value="roi">Sort by ROI 7d</option>
          <option value="trades">Sort by Trades 7d</option>
        </select>
      </div>

      {isLoading && <div>Loading…</div>}
      {error && (
        <div className="text-destructive mb-3">
          Failed to load.{" "}
          <button className="underline" onClick={() => refetch()}>
            Retry
          </button>
        </div>
      )}

      <DataGrid<CatalogRow>
        data={rows}
        columns={columns}
        pageSize={25}
        initialSorting={[{ id: "creator", desc: false }]}
        emptyLabel="No creators."
      />
    </div>
  );
}
